.main_container {
    width: 100%;
    height: 100%;
    margin-top: 120px;
  }
  .pnf {
    width: 100%;
    height: 300px;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: center;
  }
  .pnf h1 {
    font-size: 6rem;
    -webkit-background-clip: text !important;
    background-clip: text !important;
    color: transparent;
    background-position: center;
    background-size: 100%;
    filter: drop-shadow(0px 15px 20px rgba(0, 0, 0, 0.5));
  }
  .pnf h2 {
    font-weight: 300;
    letter-spacing: 10px;
    font-size: 30px;
  }
  .toHomeButton {
    padding: 10px;
    cursor: pointer;
    background-color: none !important;
    border: 2px solid;
    color: #6a3cd4;
    font-weight: 700;
    text-transform: uppercase;
    font-size: 15px;
  }
  
  .toHomeButton:hover {
    background-color: #3498db;
    color: whitesmoke;
    border: 2px solid white;
    transition: all 0.5s ease-in-out;
    border-radius: 7px;
  }
  .toHomeButton:focus {
    outline: none;
  }
  .button404 {
    text-align: center;
    margin-top: 30px;
    margin-bottom: 30px;
  }
  